import React from 'react'
import './item.css'

import clsx from 'clsx'

import { UserCritterPediaData } from 'types/critterpedia'

type Props<T extends UserCritterPediaData> = {
  critter: T
  isEditing: boolean
  onClick: () => void
}

export function Item<T extends UserCritterPediaData>({
  critter,
  isEditing,
  onClick,
}: Props<T>): JSX.Element {
  const itemModifier = isEditing
    ? critter.isDonatedToMuseum
      ? 'critter-item--museum-donated'
      : critter.isRegisteredOnCritterPedia
      ? 'critter-item--museum-donated'
      : 'critter-item--grayed-out'
    : ''
  return (
    <button className={clsx(['critter-item', itemModifier])} onClick={onClick}>
      <img
        className="critter-item__image"
        src={critter.iconUri}
        alt={critter.name['en-US']}
      />
    </button>
  )
}
