import React, { useState } from 'react'
import './CritterTable.css'

import { Item } from './Item'
import { UserCritterPediaData } from 'types/critterpedia'

type Props<T extends UserCritterPediaData> = {
  data: T[]
  updateCritterCallback: (updatedCritter: T) => void
}

export function CritterTable<T extends UserCritterPediaData>({
  data,
  updateCritterCallback,
}: Props<T>): JSX.Element {
  const [isEditing, setIsEditing] = useState<boolean>(true)

  return (
    <div className="critter-table">
      {data.map((item) => (
        <Item
          critter={item}
          key={item.id}
          isEditing={isEditing}
          onClick={() =>
            updateCritterCallback({
              ...item,
              isDonatedToMuseum: !item.isDonatedToMuseum,
            })
          }
        />
      ))}
    </div>
  )
}
